@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.App{
  margin: 0;
  height: max-content;
  box-sizing: border-box;
  background-color: pink;
}

/*==================Navigation Bar====================*/
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

.welcome-container{
  z-index: 10;
  width: 100%;
  height: 64px;
  background-color: aliceblue;
}
.welcome-left-header{
  text-align: left;
  padding-left: 10px;
  float: left;
  font-family: 'Pacifico', cursive;
  color: black;
}

ul.navigation-bar {
  z-index: 11;
  height: auto;
  padding: 0;
  margin: 0;
  display: inline;
  list-style-type: none;
  float: right;
  background-color: aliceblue;
}

.welcome-left-header, li a{
  display: block;
  
  overflow: hidden;
  
  font-size: 25px;
}

.welcome-left-bottom-header
{
  z-index: 11;
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
}

li.navigation-item {
  z-index: 11;
  float: left;
}

li a{
  z-index: 11;
  text-align: center;
  padding: 17px;
  text-decoration: none;
  font-family: 'Roboto', cursive;
  color: black;
}
li a:hover {
  display: block;
  text-align: center;
  color: black;
  background-color: pink;
}
/*==================Navigation Bar====================*/

/*==================Header Top====================*/
@media screen and (max-width: 720px) {
  .top-center-circle-text{
    width: 0;
    height: 0;
    font-size: 0;
    opacity: 0;
  }
  .top-center-circle{
    width: 10rem;
    height: 10rem;
  }
  .welcome-left-header{
    font-size: 20px;
  }
  .welcome-left-bottom-header{
    font-size: 15px;
  }
}

.top-container
{
  position: relative;
  width: auto;
}
.top-center-container-circle
{
  z-index: 3;
  position: absolute;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.img-top{
  width: auto;
  background-color: aliceblue;
}
.img-top .img-top-image
{
  position: relative;
  align-items: center;
}

.top-center-circle
{
  max-width: 17rem;
  max-height: 17rem;
  z-index: 2;
  display: block;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: unset;
}
.top-overlay-container{
  z-index: 1;
  top: 0;
  position: absolute;
  margin: 0;
  padding: 0;
  opacity: 0.8;
  background-color: pink;
  height: 99%;
  width: 100%;
}
.top-center-circle-text{
  font-family: 'Pacifico', cursive;
  font-size: 50px;
  color: black;
  text-align: center;
}
.top-center-circle-bottom-text{
  padding-top: 275px;
  font-family: 'Roboto', sans-serif;
  font-size: 45px;
  color: black;
  text-align: center;
}

/*==================Header Top====================*/

/*==================Header Center====================*/
.center-container{
  background-color: pink;
  
}

.center-text-title
{
  font-family: 'Pacifico', cursive;
  font-size: 50px;
  color: black;
  text-align: center;
}

.center-text-content p
{
  font-family: 'Roboto', sans-serif;
  font-size: 25px;
  color: black;
  text-align: left;
  margin-left: 10px;
}
/*==================Header Center====================*/

/*==================Footer====================*/
.footer{
  background-color: whitesmoke;
}
/*==================Footer====================*/
.cbImage{
  position: fixed;
  left: 100%;
  margin-left: -166px;
  margin-top: 15%;
}

div.chapterTitle
{
  text-align: center;
  font-size: 4rem;
  color: white;
}

.LongTextView{
  font-family: 'Roboto';
  font-size: 2rem;
  color: white;
  margin-left: 50px;
  margin-right: 50px;
}

p{
  padding: 2px 5px 2px 5px;
  font-family: 'Roboto';
  font-size: 2rem;
  color: white;
  margin-top: 3px;
  margin-bottom: 3px;
}
